html {
    * {
        margin: 0;
        padding: 0;
        box-sizing: border-box;

    }

    ;

    ul,
    li,
    ol,
    p,
    h4 {
        list-style: none;
        margin: 0;
        padding: 0;
    }

    a:link,
    a:visited,
    a:hover,
    a:active {
        text-decoration: none;
        cursor: pointer;
    }

    input {
        outline: none;
    }

    .clear::after {
        display: block;
        height: 0;
        visibility: hidden;
        clear: both;
        content: ".";
    }

    body {
        background-color: #eee;

        .btn-primary,
        .btn-primary:hover {
            color: #fff;
            background-color: #f13c3c;
            border-color: #f13c3c;

            &:focus {
                box-shadow: none;
            }
        }
    }
}


/* 返回顶部与客服 */
.right_sidebar {
    position: fixed;
    bottom: 25%;
    right: 0;
    z-index: 50;
    background: rgba(241, 60, 60, 0.6);

    a {
        display: block;
        width: 60px;
        padding: 53px 0 0;
        font-size: 12px;
        text-align: center;
        color: #fff !important;
        background: url(https://cdn-resources-aliyun.kjds.com/assets/right_sidebar_pic-a3eb315….png) no-repeat 5px 3px/50px 100px;
    }

    .back_top {
        padding-bottom: 10px;
        background-size: 60px 120px;
        background-position: -1px -69px;
    }
}

.home {

    .main {
        padding: 0;
        width: 1220px;
        padding-bottom: 100px;
        margin: 0 auto
    }
}

#root {
    .pagination {
        display: flex;
        padding-left: 0;
        margin: 20px 0;
        border-radius: 4px;
        text-align: right;
        justify-content: flex-end;

        li {
            display: inline-block;
            margin: 0 5px;

            a {
                position: relative;
                float: left;
                padding: 6px 12px;
                line-height: 1.42857143;
                text-decoration: none;
                color: #666;
                background-color: #ccc;
                border: 1px solid #ccc;
                margin-left: -1px;

                &:hover {
                    background-color: #f13c3c;
                    color: #fff;
                }
            }

            &.active {
                a {
                    background-color: #f13c3c;
                    border: 1px solid #f13c3c;
                    z-index: 0;
                    color: #fff;
                }
            }
        }
    }
}